export default {
	data() {
		return {
			cinemaData: null,
			markers: []
		};
	},
	onLoad(opt) {
		this.initData(opt)
	},
	methods: {
		initData(cinemaData) {
			this.cinemaData = cinemaData,
			this.markers = [{
				latitude: cinemaData.latitude,
				longitude: cinemaData.longitude
			}]
		},
		//定位自己的位置
		position() {
			const MapContext = uni.createMapContext('map')
			MapContext.moveToLocation()
		}
	},
}
